import React from 'react'
import back from "./icons8-clear-symbol-30.png"

export default function Butt(props) {
  function handdleClick(){
    if(props.value==="="){
      props.handleC("=")
    }
    else if(props.value==="(  )"){
      props.addBrackets()
    }
    else if(props.value==="X"){
      props.handleClick("*")
      
    }
    else if(props.value==="del"){
      props.del()
    }
    else if(props.value==="π"){
    props.handleCli()}
    else if(props.value==="^")
    props.handleClick("**")
    else if(props.value==="˅"){
      props.flip()
      
    }
    

   
    
  
    else{
      props.handleClick(props.value)
    }
  }
  function color(){
    if (props.value==="/"||props.value==="(  )"||props.value==="+"||props.value==="+/-"||props.value==="%"||props.value==="X"||props.value==="-"||props.value==="="){
       return "bg-blue-200 h-[87.11px] w-[87.11px] rounded-full  font-bold text-2xl text-lg mt-[6px] ml-[11px]"
    }
    else if (props.value==="AC"){
    return "bg-green-200 h-[87.11px] w-[87.11px] rounded-full  font-bold text-2xl   mt-[6px] ml-[11px]"}
    else if(props.value==="√"||props.value==="π"|| props.value==="^"|| props.value==="!"){
      return " font-bold h-[50px] w-[65px]"
    }
    else if(props.value==="˅"){
      return "w-[20px] bg-gray-200 text-xs bg-gray-100  font-bold  h-[20px] text-   mt-[6px] rounded-full ml-[40px] mt-[15px]  "
    }
    else{
      return "bg-gray-300 h-[87.11px] w-[87.11px] rounded-full font-bold text-2xl    mt-[6px] ml-[11px]"
    }
  }
  return (
    <div><button
    onClick={handdleClick}
    className={`${color()} flex justify-center items-center text-black  rounded-full    ml-[11px]  `}
    
  >
   {props.value==="del"?<img src={back} alt="image"/>:`${props.value}`}
   
  </button></div>
  )
}
