import { useState } from "react";
import "./App.css";
import Butt from "./Components/Butt";

function App() {
  const [buttons, setbuttons] = useState("");
  const [sign, setSign] = useState(true);
  const [history, setHistory] = useState(false);
  const [re, setRe] = useState([""]);
  function flip() {
    setHistory((x) => !x);
  }

  function del() {
    setbuttons(buttons.substring(0, buttons.length - 1));
  }

  function handleChange(event) {
    setbuttons(event.target.value);
  }
  function handleRoot(a) {
    try {
      let res = eval(buttons);
      setbuttons(Math.sqrt(res));
    } catch (error) {
      setbuttons("Error");
    }
  }
  function handleClick(a) {
    if (a === "AC") {
      setbuttons("");
    } else if (a === "!") {
      if (sign === true) {
        setbuttons((x) => ["-" + x]);
        setSign(false);
      } else {
        setbuttons(buttons.slice(1));
        setSign(true);
      }
    } else if (a === "√") {
      handleRoot(buttons);
    } else {
      setbuttons((prev) => prev + a);
    }
  }
  function handleC() {
    try {
      const results = eval(buttons);
      setRe((x) => [x] + results + " , ");

      setbuttons(results);
    
    } catch (error) {
      setbuttons("Error");
    }
  }

  function addBrackets() {
    const a = "(" + buttons + ")";
    
    setbuttons(a);
  }
  function handleCli() {
    try {
      if (buttons !== "") {
        const ro = eval(buttons);
        setbuttons(3.14 * ro);
      } else {
        setbuttons((x) => ["3.14" + x]);
      }
    } catch (Error) {
      setbuttons("Error");
    }
  }

  const handleKeyPress = (event) => {
    try {
      if (event.key === "Enter") {
        const result = eval(buttons);
        setRe((x) => [...x] + result);
        setbuttons(result);
      }
    } catch (error) {
      setbuttons("Error");
    }
  };
  const but = [
    "√",
    "π",
    "^",
    "!",
    "˅",
    "AC",
    "(  )",
    "%",
    "/",
    "1",
    "2",
    "3",
    "X",
    "4",
    "5",
    "6",
    "-",
    "7",
    "8",
    "9",
    "+",
    "0",
    ".",
    "del",
    "=",
  ];
  const newButtons = but.map((x) => {
    return (
      <Butt
        value={x}
        handleClick={handleClick}
        handleCli={handleCli}
        flip={flip}
        handleC={handleC}
        key={x}
        addBrackets={addBrackets}
        del={del}
      />
    );
  });
  let value;
  if (
    buttons[buttons.length - 1] === "+" ||
    buttons[buttons.length - 1] === "-" ||
    buttons[buttons.length - 1] === "*" ||
    buttons[buttons.length - 1] === "/" ||
    buttons[buttons.length - 1] === "%" ||
    buttons[buttons.length - 1] === ")" ||
    buttons[buttons.length - 1] === "."
  ) {
    value = buttons;
  } else if (
    buttons[buttons.length - 1] === ")" + "+" ||
    buttons[buttons.length - 1] === ")" + "-" ||
    buttons[buttons.length - 1] === ")" + "*" ||
    buttons[buttons.length - 1] === ")" + "/" ||
    buttons[buttons.length - 1] === ")" + "%" ||
    buttons[buttons.length - 1] === ")" + "("
  ) {
    value = buttons;
  } else {
    try {
      value = eval(buttons);
      
    } catch (error) {
      setbuttons("Error");
    }
  }
  function width() {
    if (buttons !== "") {
      return "h-[139px] ";
    } else if (history === false) {
      return "h-[192px] rounded-b-2xl";
    } else if (history === true) {
      return "h-[167px]";
    } else {
      return "h-[167px] rounded-b-2xl";
    }
  }
  return (
    <div className="h-screen w-screen bg-sky-100 flex justify-center items-center">
      <div className=" h-[710px] w-[400px]  bg-gray-100">
        <input
          type="text"
          value={buttons}
          onChange={handleChange}
          onKeyPress={handleKeyPress}
          className={`w-[400px] ${width()} bg-gray-200 overflow-x-scroll focus:outline-none   text-end font-[200px] pt-[20px] pl-[00px] flex flex-col  text-[57px] text-black`}
        ></input>
        {buttons !== "" && (
          <div
            className={`${
              history ? "h-[25px]" : "h-[50px]"
            } w-[400px] mb-[3px] bg-gray-200  text-end rounded-b-2xl `}
          >
            {value}
          </div>
        )}

        {history && (
          <div className=" h-[25px] w-[400px] relative text-black">{re}</div>
        )}
        <div className="h-[465px] flex flex-wrap w-[393px]    bg-gray-100">
          {newButtons}
        </div>
      </div>
    </div>
  );
}

export default App;
